<template>
  <div class="student-container">
    <el-card class="info-card">
      <h3>üë§ ‰∏™‰∫∫‰ø°ÊÅØ</h3>
      <el-descriptions :column="1" border>
        <el-descriptions-item label="ÂßìÂêç">{{ studentInfo.studentName }}</el-descriptions-item>
        <el-descriptions-item label="Â≠¶Âè∑">{{ studentInfo.studentNumber }}</el-descriptions-item>
        <el-descriptions-item label="Áè≠Á∫ß">{{ studentInfo.classes }}</el-descriptions-item>
        <el-descriptions-item label="‰∏ì‰∏ö">{{ studentInfo.major }}</el-descriptions-item>
      </el-descriptions>
    </el-card>

    <el-card class="wenxue-card">
      <h3>üìä ÂΩìÂâçËØæÁ®ãÈóÆÂ≠¶ÂÄº</h3>
      <div v-loading="loading">
        <el-progress 
          :percentage="wenxueValue"
          :format="formatProgress"
          status="success"
          stroke-width="15"
        />
        <div class="wenxue-detail">
          ËØæÁ®ãÂêçÁß∞Ôºö{{ currentCourseName }}
          <br>
          ÈóÆÂ≠¶ÂÄºÂæóÂàÜÔºö{{ wenxueValue }} ÂàÜ
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { getStudentInfo, getWenxueValue } from '@/api/student'

export default {
  data() {
    return {
      studentInfo: {},
      wenxueValue: 0,
      loading: true,
      currentCourseId: localStorage.getItem('currentCourseId') || ''
    }
  },
  computed: {
    currentCourseName() {
      return this.$store.getters['chapter/getCurrentChapterParams']?.courseName || 'Êú™ÈÄâÊã©ËØæÁ®ã'
    }
  },
  async mounted() {
    await this.fetchStudentInfo();
    if(this.currentCourseId) {
      await this.fetchWenxueValue();
    } else {
      this.$message.warning('ÂΩìÂâçÊú™ÈÄâÊã©ËØæÁ®ãÔºåÊó†Ê≥ïËé∑ÂèñÈóÆÂ≠¶ÂÄº');
    }
    this.loading = false;
  },
  // Ê∑ªÂä†ËØæÁ®ãIDÂèòÂåñÁöÑÁõëÂê¨
  watch: {
      '$store.state.chapter.currentChapterParams': {
      handler(newVal) {
        if(newVal?.courseId) {
          this.currentCourseId = newVal.courseId;
          localStorage.setItem('currentCourseId', newVal.courseId);
        }
      },
      deep: true
    }
  },
  methods: {
    // Âú®ÊñπÊ≥ï‰∏≠ÁßªÈô§Êó†ÊïàÂ≠óÊÆµÂºïÁî®
    async fetchStudentInfo() {
      try {
        const res = await getStudentInfo()
        if(res.data?.code === 1) {
          this.studentInfo = res.data.data
          // Â≠òÂÇ®ÂøÖË¶Å‰ø°ÊÅØÂà∞localStorage
          // localStorage.setItem('student-info', res.data.data.studentName)
        }
      } catch (error) {
        this.$message.error('Ëé∑ÂèñÂ≠¶Áîü‰ø°ÊÅØÂ§±Ë¥•')
      }
    },
    async fetchWenxueValue() {
      try {
        const res = await getWenxueValue(this.currentCourseId)
        if(res.data?.code === 1) {
          this.wenxueValue = res.data.data.wenxueValue || 0  // ‚Üê ‰øÆÊîπÂ≠óÊÆµÂêçÁß∞
          this.$message.success('ÈóÆÂ≠¶ÂÄºËé∑ÂèñÊàêÂäü')
        }
      } catch (error) {
        this.$message.error('Ëé∑ÂèñÈóÆÂ≠¶ÂÄºÂ§±Ë¥•')
      }
    }
  }
}
</script>

<style scoped>
.student-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
}

.info-card, .wenxue-card {
  margin-bottom: 20px;
}

.wenxue-detail {
  margin-top: 15px;
  padding: 10px;
  background: #f8f9fa;
  border-radius: 6px;
}

.el-progress {
  margin: 20px 0;
}
</style>